import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  signUp = false;
  borderOne = false;
  borderTwo = true;
  red = false;
  transparent = true;
  signLoginForm = true;
  constructor() {}

  ngOnInit() {
  }

  SignUpBtn(){
    this.signUp = true;
    this.red = true;
    this.transparent = false;
  }
  LoginBtn(){
    this.signUp = false;
    this.red = false;
    this.transparent = true;
  }
  password(){
    this.signLoginForm = false;
  }
} 
